.catalog__item {
    flex: 0 1 33.333%;
    margin: auto 0 0;
    padding: 0.8%;
}

.item__image {
    margin: auto;
    max-width: 204px;
    border-radius: 7px;
    border: 2px solid #5e2431;
    object-fit: contain;
}

.image__content {
    max-width: 200px;
    border-radius: 7px;
}